/* ## VARIABLES ############################# */
$default: #666;
$background: #fff;

/* ## FONTS ################################# */
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,700');
@import url('https://fonts.googleapis.com/css?family=Ubuntu');

@import 'css/vendor/bootstrap/css/bootstrap.min';
@import 'css/fontawesome/font-awesome';
@import 'css/vendor/animate/animate.min';
@import 'css/vendor/linear-icons/css/linear-icons.min';
@import 'css/vendor/owl.carousel/assets/owl.carousel.min';
@import 'css/vendor/owl.carousel/assets/owl.theme.default.min';
@import 'css/vendor/magnific-popup/magnific-popup.min';

@import 'css/vendor/theme';
@import 'css/vendor/theme-elements';

@import	'css/vendor/rs-plugin/css/settings';
@import	'css/vendor/rs-plugin/css/layers';
@import	'css/vendor/rs-plugin/css/navigation';

@import	'css/vendor/skins/default';
@import	'css/vendor/custom';

/* === HEADER ===*/

#header .header-logo img {
    height: auto;
    width: 50%;
    object-fit: cover; 
}
#header.header-container-no-min-height .header-container {
    min-height: auto!important;
    height: 150px!important;
}

/* === HOME  ==== */

.section {
	.text-light-color {
		h2 { 
			color: #fff;
		}
		p {
			color: #f2f2f2;
		}
	}
	.top-sub-title, a.btn-svc {     
		text-transform: uppercase;
	}
} 

/* === OBRAS REALIZADAS === */
.badge {
	font-size: 100%!important;
}

/* === MODAL === */
.modal-dialog {
	max-width: 900px;
	.row {
		width: 100%;
	}
	.contentmodal {
		margin: 40px 15px 10px;
		text-align: center;
		h3 {
			color: #000!important;
			font-size: 30px!important;
		}
		span {
			color: #2388ed!important;
			text-transform: uppercase;
		}	
	}
}





